<template>
    <div class="container mt-5">
        <div v-if="product && product.id">
            <div class="product-detail">
                <img :src="productImageUrl" alt="Product Image" class="product-image-large">
                <h2>{{ product.name }}</h2>
                <p>{{ product.description }}</p>
                <p>價格：{{ product.price }} 元</p>
                <button class="btn btn-primary" @click="addToCart">加入購物車</button>
            </div>
        </div>
        <div v-else>
            <p>正在加載商品中...</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
    name: 'ProductDetail',
    data() {
        return {
            product: null,
            isLoggedIn: false,
        };
    },
    computed:{
        productId(){
            return this.$route.params.id;
        },
        productImageUrl(){
            return this.product ? `/api/products/${this.productId.image}` : '';
        }
    },
    mounted() {
        this.fetchProduct();
        this.checkLoginStatus();
    },
    methods: {
        async fetchProduct() {
            try {
                const response = await axios.get(`/api/products/${this.productId}`);
                console.log("API 返回的產品資料：", response.data);
                this.product = response.data;
            } catch (error) {
                console.error("加載商品詳情時出錯", error);
                alert("加載商品詳情時出錯，請稍後再試！");
            }
        },
        checkLoginStatus() {
            this.isLoggedIn = !!localStorage.getItem('authToken');
        },        
        ...mapActions('cart',['addProductToCart']),
        addToCart() {
            if (!this.isLoggedIn) {
                alert('請先登入再加入購物車！');
                const returnPath = this.$route.fullPath;
                this.$router.push({
                    name:'UserLogin',
                    query:{ redirect: returnPath}
                });
                return;
            }
            this.addProductToCart(this.product);
            alert(`已將${this.product.name}加入購物車`);
        },
    },
};
</script>

<style scoped>
.product-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.product-image-large {
    width: 300px;
    height: 300px;
    object-fit: cover;
    margin-bottom: 20px;
}
</style>
