<template>
        <div class="product-card">
            <router-link :to="{name: 'ProductDetail',params:{id:productId}}">
            <img :src="productImagerUrl" class="product-image" alt="Product Image" @error="imageError">
            <h5 class="card-title">{{ product.name }}</h5>
            <p class="card-text">{{ product.price }} 元</p>  
            </router-link>
            <button class="btn btn-primary" @click="handleAddToCart">加入購物車</button>
        </div>
</template>
    
<script>

export default {
    name: 'ProductCard',
    props:{
        product:{
            type:Object,
            required:true
        }
    },
    computed:{
        productId(){
            return String(this.product.id).trim();
        },
        productImagerUrl(){
            return `/img/product/${this.product.image}`;
        }
    },
    mounted(){
        console.log("ProductCard 商品:",this.product);
    },
    methods: {
        handleAddToCart(){
            this.$emit('add-to-cart',this.product);
        },
        imageError(event) {
        event.target.src = '/img/wrong.png'; // 預設的佔位圖片
    },
    },
};

</script>

<style scoped>
.product-card {
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 8px;
    text-align: center;
}
.product-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-bottom: 10px;
}
.btn-primary {
    background-color: #007bff;
    border: none;
    color: white;
}
</style>
